$color-event-new: $color-info;
$color-event-working: $color-warning;
$color-event-done: $color-success;
$color-event-canceled: $color-danger;
// $read: ;
// $filled: ;

@mixin eventColor($color) {
    border-color: $color;

    .dhx_header,
    .dhx_title,
    .dhx_body,
    .dhx_footer {
        color: $gray--darken;
        background-color: lighten($color, 30%);
    }
}

@mixin eventTitleIcon($icon, $color) {

    .dhx_title {

        &::before {
            content: $icon;
            @include fa('light');
            color: $color;
        }
    }
}

.calendar {
    padding: 0;

    &__top-line {
        padding: 30px 30px 0;

        @include respond-to(md) {
            padding: 0;
        }

        .bb-checkbox-row {
            max-width: 300px;
            margin-left: 40px;
            flex-grow: 1;
        }

        .bb-checkbox {

            &__title {
                height: 40px;
                line-height: 33px;
                font-size: 15px;
            }
        }
    } //.cabinet__top-line

    &__sorting {
        @include respond-to(md) {
            flex-wrap: wrap;
        }
    } //.cabinet__sorting

    &__date {
        margin: auto;

        @include respond-to(md) {
            margin: 0;
        }
    } //.cabinet__date

    .control-box {
        margin-right: 40px;

        @include respond-to(md) {
            margin-right: 0;
            margin-bottom: $gutter;
        }

        @include respond-to(xs) {
            margin-right: 0;
        }
    } //.cabinet .control-box

    .top-line {

        &__select {
            min-width: 270px;
            margin-bottom: 0;
        }
    }
} //.cabinet

.c-calendar {
    min-height: 300px;
    height: 100%;
    position: relative;

    &__btn {
        background-color: transparent;
        border: 0;
        cursor: pointer;
        outline: none;
        position: absolute;
        top: 15px;
        z-index: 3;

        &:hover {

            i {
                color: $gray;
            }
        }

        &--prev {
            left: 45px;
        } //.c-calendar__btn--prev

        &--next {
            right: 23px;
        } //.c-calendar__btn--next

        i {
            font-size: 28px;
            color: $gray--lighten;
        }
    } //.c-calendar__btn

    &__empty {
        padding-top: 50px;
    } //.c-calendar__empty

    .user {

        &__img {
            margin: 5px auto;
        } //.c-calendar .user __img

        &__name,
        &__surname {
            font-family: $f-medium;
            font-size: 14px;
            color: $color-text;
        } //.c-calendar .user __name, .c-calendar .user __surname
    } //.c-calendar .user

    .dhx_cal_container {
        font-family: $f;
        background-color: transparent;
        overflow: inherit;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    } //.c-calendar .dhx_cal_container

    .dhx_cal_header,
    .dhx_scale_bar {
        border: 0;
        background: transparent;
    } //.c-calendar .dhx_cal_header, .c-calendar .dhx_scale_bar

    .dhx_cal_tab {

        &.week_tab {
            border-radius: 0 $border-radius $border-radius 0;
        }
    } //.c-calendar .dhx_cal_tab

    .dhx_cal_next_button,
    .dhx_cal_tab_last {
        border-radius: 0 $border-radius $border-radius 0;
    } //.c-calendar .dhx_cal_next_button, .c-calendar .dhx_cal_tab_last

    .dhx_cal_next_button,
    .dhx_cal_prev_button {
        top: 75px;
        border: 0;
        background: none;

        &::before {
            content: '\f105';
            @include fa('light');
            font-size: 28px;
            color: $gray--lighten;
            @include tr;
            @include centre;
        }

        &:hover {

            &::before {
                color: $gray;
            }
        }
    } //.c-calendar .dhx_cal_next_button, .c-calendar .dhx_cal_prev_button

    .dhx_cal_prev_button {
        left: 35px;

        &::before {
            content: '\f104';
        }
    } //.c-calendar .dhx_cal_prev_button

    .dhx_cal_prev_button,
    .dhx_cal_tab_first {
        border-radius: $border-radius 0 0 $border-radius;
    } //.c-calendar .dhx_cal_prev_button

    .dhx_cal_today_button {
        border-radius: $border-radius;
    } //.c-calendar .dhx_cal_today_button

    .dhx_cal_data {
        background-size: 8px 8px;
        background-image: linear-gradient(
            45deg,
            transparent 46%,
            rgba(36, 51, 74, 0.2) 49%,
            rgba(36, 51, 74, 0.2) 51%,
            transparent 55%
        );

        // background-image:  url('../libs/calendar/imgs_dhx_terrace/bb_databg_now.png');
        background-color: $color-bg--lightest;
    } //.c-calendar .dhx_cal_data

    .dhx_scale_holder,
    .dhx_scale_holder_now {
        background-image: url('../libs/calendar/imgs_dhx_terrace/td_bg.png');

        &:hover {
            cursor: cell;
        }
    } //.c-calendar .dhx_scale_holder_now

    .dhx_time_reserved {
        // background-image: url('../libs/calendar/imgs_dhx_terrace/bb_databg_now.png');
        background-image: url('../libs/calendar/imgs_dhx_terrace/databg.png');
    } //.c-calendar .dhx_time_reserved

    //   

    .dhx_cal_event {
        margin-left: -1px;
        border-radius: $border-radius;

        &:hover {
            cursor: pointer;
        }

        &:not(.dhx_cal_select_menu) {
            border-left: 4px solid transparent;

            .dhx_title {
                position: relative;

                &::before {
                    position: absolute;
                    right: 5px;
                }
            }

            &.event_new {
                @include eventColor($color-event-new);
                @include eventTitleIcon('\f118', $color-event-new);
            } //.dhx_cal_event.event_new

            &.event_working {
                @include eventColor($color-event-working);
                @include eventTitleIcon('\f017', $color-event-working);
            } //.dhx_cal_event.event_working

            &.event_done {
                @include eventColor($color-event-done);
                @include eventTitleIcon('\f00c', $color-event-done);
            } //.dhx_cal_event.event_done

            &.event_canceled {
                @include eventColor($color-event-canceled);
                @include eventTitleIcon('\f119', $color-event-canceled);
            } //.dhx_cal_event.event_canceled
        }

        .dhx_header,
        .dhx_title,
        .dhx_body,
        .dhx_footer {
            color: $color-text;
            background-color: #fff;
            border: 0;
        }

        .dhx_title {
            width: calc(100% - 10px);
            padding: 5px 5px 0;
            font-family: $f-num--b;
            text-align: left;
            border-top-left-radius: 0;
            border-top-right-radius: $border-radius;
        }

        .dhx_body {
            border-bottom-left-radius: 0;
            border-bottom-right-radius: $border-radius;
        }
    } //.dhx_cal_event

    //  

    .dhx_menu_head,
    .dhx_menu_icon {
        background-image: url('../libs/calendar/imgs_dhx_terrace/controls--black.png');
    }

    .dhx_cal_select_menu,
    .dhx_cal_editor {
        display: none;
    }
} //.c-calendar
