.aplication {
    height: 100%;
    @include flexColumn;

    &:not(.aplication--new) {

        &.is-focus {

            .aplication {

                &__right {
                    box-shadow: -3px 3px 15px rgba(0, 0, 0, 0.2);
                }
            }

            .overlay {
                display: block;
            }
        }
    }

    &__wrap {
        border-top: 1px solid $color-border--lightest;
        overflow: hidden;
        display: flex;
        flex-grow: 1;

        // @include respond-to(sm) {
        //     border-top: 0;
        //     overflow-y: auto;
        //     flex-wrap: wrap;
        // }
    } //.aplication__wrap

    &__left,
    &__right {
        position: relative;
        overflow-x: hidden;
        @include flexColumn;

        &.w50 {
            max-width: 100%;
            min-width: auto;
            flex: 1 1 50%;
        }
    }

    &__left {
        @include bb-scroll--thin;
        flex: 1 1 66.7%;

        // @include respond-to(sm) {
        //     margin-right: 0;
        //     padding: 0;
        //     flex-basis: 100%;
        // }

        &.bg-light {
            background-color: #fbfbfb;
        }
    } //.aplication__left

    &__right {
        min-width: 425px;
        max-width: 425px;
        border-left: 1px solid $color-border--lightest;
        position: relative;
        flex: 0 0 33.3%;

        @include respond-to(md) {
            min-width: 400px;
        }

        @include respond-to(sm) {
            min-width: 375px;
        }

        // @include respond-to(sm) {
        //     flex-basis: 100%;
        //     min-width: 100%;
        //     max-width: 100%;
        //     background-color: #fff;
        //     position: fixed;
        //     top: 0;
        //     left: 0;
        //     right: 0;
        //     bottom: 0;
        //     z-index: z(fixed);
        //     display: none;
        // }
    } //.aplication__right

    &__in {
        @include bb-scroll--thin;
        flex-grow: 1;
    } //.aplication__in

    &__inner {
        max-width: 900px;
        width: 100%;
        margin: 0 auto;
        padding: 20px 20px 20px 27px;

        @include respond-to(sm) {
            padding: 23px 15px;
        }

        // @include respond-to(sm) {
        //     padding: 0;
        //     overflow-x: hidden;
        //     overflow-y: auto;
        //     position: absolute;
        //     top: 0;
        //     left: 0;
        //     right: 0;
        // }
    } //.aplication__inner

    &__info-line {
        margin-bottom: 25px;
        @include flexCenter;

        @include respond-to(sm) {
            flex-direction: column;
            align-items: flex-start;
        }

        .select-color {
            min-width: 250px;
            margin-left: auto;
            flex-shrink: 0;

            @include respond-to(sm) {
                min-width: 100%;
                margin-left: 0;
                margin-top: 20px;
            }
        } //.aplication__info-line .select-color

        .select2-container
        .select2-selection--single
        .select2-selection__rendered {
            padding-right: 0;
        }
    } //.aplication__info-line

    &__header {
        height: 60px;
        position: relative;
        @include flexCenter;
        flex-shrink: 0;
    } //.aplication__header

    &__title {
        display: block;
        width: 100%;
        font-family: $f-light;
        font-weight: 300;
        font-size: 28px;
        color: $gray--darkest;
        text-align: center;
    } //.aplication__title

    &__close {
        @include centre('y');
        right: 14px;

        &:hover {

            .icon {
                fill: $gray--darkest;
            }
        }

        .icon {
            width: 20px;
            height: 20px;
        }
    } //.aplication__close

    &__top-line {
        padding: $gutter $gutter * 1.5;
        margin-bottom: 0;
        flex-shrink: 0;

        @include respond-to(sm) {
            margin-top: 5px;
            margin-bottom: 0;
            padding: 0;
            background-color: #fff;
            border-top: 1px solid $color-border--lightest;
            border-bottom: 1px solid $color-border--lightest;
            align-items: initial;
        }

        .top-line {

            &__sorting {
                @include respond-to(sm) {
                    margin-right: 0;
                    padding: $gutter / 2;
                }
            }

            &__select {
                @include respond-to(md) {
                    margin: 0;
                }
            }
        }

        .mobile-item {
            margin: 0;

            &--client {
                @include border(left);
            }
        }
    } //.aplication__top-line

    &__user {
        display: none;
        padding: $gutter / 2 25px;
        cursor: pointer;

        &:hover {
            background-color: #f9f9f9;
        }

        & + & {
            border-top: 1px solid $color-border--lightest;
        }
    } //.aplication__user

    &__text {
        margin-top: $gutter * 1.5;
        padding-left: 28px;

        @include respond-to(sm) {
            padding-left: 0;
        }
    } //.aplication__text

    &__btns {
        margin-top: auto;
        padding: $gutter;
        border-top: 1px solid $color-border--lightest;
        @include flexColumn;
        justify-content: center;
        flex-shrink: 0;

        // @include respond-to(sm) {
        //     background-color: #fff;
        //     position: fixed;
        //     left: 0;
        //     bottom: 0;
        //     right: 0;
        //     z-index: z(fixed);
        // }

        .text {
            margin: auto auto $gutter;

            i {
                font-size: 15px;
            }
        } //.aplication__btns .text

        .btn {
            min-width: 160px;
            padding: 9px 10px;

            & + .btn {
                margin-left: 20px;
            }
        } //.aplication__btns .btn

        .button-box {
            @include flexCenter;
        } //.aplication__btns .button-box
    } //.aplication__btns

    &__date {
        width: auto;
        margin-left: 30px;
        font-family: $f-light;
        font-size: 23px;
        position: relative;

        @include respond-to(sm) {
            margin-left: 0;
        }

        .bb-input {

            &__icon {
                // color: $color-main;
                right: auto;
                left: -10px;
            } //.aplication__date .bb-input__icon
        } //.aplication__date .bb-input
    } //.aplication__date

    &__btn {

        &--link {
            padding: 15px 0;
            font-size: 15px;
            border-radius: 0;
            border-bottom: 1px solid $color-border--lightest;
        } //.aplication__btn--link
    } //.aplication__btn

    .top-line {

        &__select {
            margin-right: 0;
            margin-left: 205px;

            @include respond-to(lg) {
                margin-left: 20px;
            }
        } //.aplication .top-line__select
    } //.aplication .top-line

    .empty {
        padding: 50px 20px;
        font-size: 14px;

        &__text {
            text-align: center;
        } //.aplication .empty__text
    } //.aplication .empty

    &--new {

        .aplication {

            &__inner {
                padding: 50px;
            } //.aplication--new .aplication__inner
        }

        .bb-input {
            margin-bottom: 20px;
        } //.aplication--new .bb-input
    } //.aplication--new

    &--edit {

        &.is-focus {

            .user {

                &__arrow {
                    display: none;
                }

                &__info-block {
                    display: block;
                }
            }

            .aplication-success__tab {
                @include trfX(0);
            }
        } //.aplication--edit.is-focus

        .user {

            &__info-block {
                display: none;
            } //.aplication--edit .user__info-block
        } //.aplication--edit .user

        .aplication {

            &__date {
                margin-left: 0;
            } //.aplication--edit .aplication__date
        } //.aplication--edit .aplication

        .aplication-success {
            overflow: hidden;
            position: relative;

            &__tab {
                background-color: #fff;
                position: absolute;
                top: 91px;
                right: 0;
                bottom: 0;
                width: 100%;
                transition: $trs;
                @include trfX(200%);
            } //.aplication--edit .aplication-success__tab

            &__user {
                // padding: 25px 20px;

                &:hover {
                    background-color: #fbfbfb;
                }
            } //.aplication--edit .aplication-success__user

            .tab {

                &__content {
                    position: static;
                } //.aplication--edit .tab__content

                &__inner {
                    padding: 20px;
                    position: absolute;
                    top: 53px;
                    left: 0;
                    width: 100%;
                    bottom: 0;
                } //.aplication--edit .tab__inner
            }
        } //.aplication--edit .aplication-success
    } //.aplication--edit
} //.aplication

.aplication-item {
    padding: 20px;
    position: relative;
    @extend %box;

    & + & {
        margin-top: 30px;

        @include respond-to(sm) {
            margin-top: 20px;
        }
    }

    &__wrap {
        padding-left: 28px;
        border-left: 2px dashed $color-border--lightest;

        @include respond-to(sm) {
            padding-left: 0;
            border-left: 0;
        }
    } //.aplication-item__wrap

    &__row {
        margin-bottom: $gutter / 2;
        position: relative;
        @include row-flex;

        @include respond-to(sm) {
            flex-direction: column;

            &:first-child {

                .aplication-item__col {

                    &:first-child {
                        margin-top: 10px;
                        order: 1;
                    }
                }
            }
        }
    } //.aplication-item__row

    &__col {
        @include col;
        @include flexCenter;

        &:first-child {
            @include size(7);
            @include size-sm(12);
            flex: 1;
        }

        &:last-child {
            @include size(5);

            .bb-input {

                &--select {
                    max-width: 143px;

                    @include respond-to(md) {
                        max-width: 100%;
                    }
                }
            }
        }

        .bb-input {
            width: 100%;

            &__wrap {
                width: 100%;
                display: flex;
                align-items: flex-start;
            } //.aplication-item__col .bb-input__wrap
        } //.aplication-item__col .bb-input
    } //.aplication-item__col

    &__service {

        .aplication-item__col {
            @include size-md(12);

            & + .aplication-item__col {
                @include respond-to(md) {
                    margin-top: $gutter / 2;
                }
            }
        }
    } //.aplication-item__service

    &__btn {

        &:hover {
            text-decoration: none;
        }

        &--edit {
            font-size: 14px;
            margin-top: 22px;
            color: $gray--light;

            &:hover {
                color: $gray;
            }

            @include respond-to(md) {
                margin-top: 0;
            }
        } //.aplication-item__btn--edit

        &--add {
            margin-top: 10px;
            font-size: 13px;
        } //.aplication-item__btn--add

        &--reset {
            position: absolute;
            top: 5px;
            right: 6px;

            &:hover {

                .btn {
                    text-decoration: none;
                    opacity: 1;
                    transform: rotate(-90deg);
                }
            }

            .btn {
                padding: 5px;
                font-size: 16px;
                opacity: 0.6;
                @include tr;

                i {
                    margin-right: 0;
                }
            }
        } //.aplication-item__btn--reset
    } //.aplication-item__btn

    &__dell {
        border: 0;
        cursor: pointer;
        background-color: transparent;
        position: absolute;
        top: -3px;
        left: -15px;

        i {
            color: $color-danger;
            opacity: 0.6;
            @include tr;
        }

        &:hover {

            i {
                opacity: 1;
                transform: rotate(90deg);
            }
        }
    } //.aplication-item__dell

    &__counter {
        width: 25px;
        height: 25px;
        color: $color-main;
        background-color: #fff;
        border: 2px solid $color-main;
        border-radius: 50%;
        position: absolute;
        top: 52px;
        left: -42px;
        @include flexCenter;
        justify-content: center;

        @include respond-to(sm) {
            display: none;
        }
    } //.aplication-item__counter

    &--short {
        border: 1px solid $color-border--lightest;
        background-color: $color-bg--lightest;
        box-shadow: none;

        .aplication-item {

            &__btn {

                &--add {
                    display: none;
                }
            }

            &__counter {
                border-color: #e4e4e4;
            }
        }
    } //.aplication-item--short
} //.aplication-item

.aplication-success {
    background-color: #fff;
    position: relative;
    overflow-y: hidden;

    &__user {
        padding: 25px 20px;
        border-bottom: 1px solid $color-border--lightest;

        &:hover {

            .user__arrow {
                color: $gray--light;
            }
        }
    } //.aplication-success__user

    &__status {
        width: 100%;
        @extend .search-client__form;
        display: flex;

        .select2-container {
            max-width: 285px;
            margin: auto;
        }

        .select2-container--default .select2-selection--single {
            background-color: #fff;
        }
    } //.aplication-success__stauts

    &__tab {
        padding: 20px 20px 0;
    } //.aplication-success__tab

    .tab {

        &__inner {
            padding: 20px 20px 0;
        } //.aplication-success .tab__inner

        &__title {
            font-family: $f-medium;
            font-size: 14px;
        } //.aplication-success .tab__title

        &__link {
            padding: 5px;
        } //.aplication-success .tab__link

        &__content {
            padding: 0;
            @include flexColumn;
            position: absolute;
            top: 145px;
            left: 0;
            right: 0;
            bottom: 0;
        } //.aplication-success .tab__content
    } //.aplication-success .tab

    .user {

        &__arrow {
            margin-left: auto;
            font-size: 25px;
            color: $gray--lighten;
        } //.aplication-success .user__arrow
    } //.aplication-success .user

    .info-block {

        &__link {
            font-family: $f-medium;
        }
    } //.aplication-success .info-block
} //.aplication-success

.aplication-info-table {

    &__list {
        @include flexColumn;
    } //.aplication-info-table__list

    &__item {
        margin-bottom: 20px;
        display: flex;
    } //.aplication-info-table__item

    &__text {
        @include flexColumn;
        flex: 1;
    } //.aplication-info-table__text

    &__title {
        font-family: $f-medium;
    } //.aplication-info-table__title

    &__desc {
        font-size: 15px;
        color: $gray--light;
    } //.aplication-info-table__desc

    &__time,
    &__price {
        font-family: $f-num--m;
        max-width: 100px;
        flex: 0 0 100px;
    }

    &__time {
    } //.aplication-info-table__time

    &__price {
        text-align: right;
    } //.aplication-info-table__price

    &__result {
        padding-left: 100px;
        padding-top: 20px;
        @include border(top);
        display: flex;

        .aplication-info-table {

            &__desc {
                font-size: 17px;
                color: $gray--light;
                flex: 1;
            }
        }
    } //.aplication-info-table__result
} //.aplication-info-table

.search-client {
    @include flexColumn;

    &__form {
        padding: 25px;
        background-color: $color-bg--lightest;
        border-bottom: 1px solid $color-border--lightest;

        .search {

            &__input {
                border: 1px solid $color-border--lightest;

                &:hover {
                    border-color: $color-border--light;
                }
            }
        } //.search-client__form .search
    } //.search-client__form
} //.search-clien

.new-client {
    background-color: #fff;
    @include bb-scroll--thin;
    @include coverdiv;
    z-index: z(fixed);
    @include tr;
    @include trfX(1000%);
    will-change: transform;

    &.is-open {
        @include trfX(0);
    }

    &__form {
        padding: $gutter $gutter * 1.5;
    } //.new-client__form

    &__title {
        margin-bottom: $gutter / 2;
        font-family: $f-medium;
        font-size: 18px;
        color: $gray--darkest;
    } //.new-client__title

    &__btn {
        display: inherit;
        margin: auto;
    } //.new-client__btn

    .bb-input {
        width: 100%;
        margin-bottom: $gutter;

        &__input {
            width: 100%;
        }
    } //.new-client .bb-input

    .bb-text {
        margin-bottom: $gutter * 2;
    } //.new-client .bb-text
} //.new-client

.new-aplication {

    .bb-input--price {

        &::before {
            right: 4px;
        }
    } // .new-aplication .bb-input--price

    .select2-container--default
    .select2-selection--single
    .select2-selection__arrow
    b {
        right: 3px;

        &::before {
            color: $color-main;
        }
    }
} // .new-aplication
